body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* VARIOUS */

.Card {
  background: greenyellow;
  width: 150px;
  padding: 5px;
  margin: 5px;
  box-shadow: 0 0 4px 2px black;

}

.StatusWrapper {
  background: white;
  min-height: 200px;
}

.NameWrapper {
  background: white;
  min-height: 50px;
}

.CoachWrapper {
  display: flex;
  justify-content: space-around;
  align-items: center;
}

.NameWrapper:focus {
  transform: scale(1.1);
}

.main_background {
  height: 100vh;
  width: 100vw;
  background-image: url('Landscape.jpg');
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
/* END VARIOUS */


/* NAVBAR */ 
:root {
  --bg: #1C110A;
  --bg-accent: #E4D6A7;
  --text-color: #50A2A7;
  --nav-size: 60px;
  --border: 1px solid #474a4d;
  --border-radius: 8px;
  --speed: 500ms;
  --boxshadow: 0 0 3px 3px black;
}

* {
  margin: 0;
  padding: 0;
  overflow: hidden;
}
/* NAV BAR  */

ul {
  list-style: none;
  margin: 0;
  padding: 0;

}

a {
  color: var(--text-color);
  text-decoration: none; 
}

.navbar {
  height: var(--nav-size);
  background-color: var(--bg);
  padding: 0 1rem;
  border-bottom: var(--border);
  margin-bottom: 40px;
}

.navbar-nav {
  max-width: 100%;
  height: 100%;
  display: flex;
  justify-content: flex-end;
}

.nav-item {
  width: calc(var(--nav-size)* 0.8);
  display: flex;
  align-items: center;
  justify-content: center;
}

.icon-button {
  --button-size: calc(var(--nav-size) * 0.5);
  width: var(--button-size);
  height: var(--button-size);
  background-color: var(--bg-accent);
  border-radius: 50%;
  padding: 5px;
  margin: 2px;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: filter 300ms;
}

.icon-button:hover {
  filter: brightness(1.2);
}

.icon-button svg {
  fill: var(--text-color);
  width: 20px;
  height: 20px;
}

.dropdown {
  position: absolute;
  top: 58px;
  width: 300px;
  transform: translateX(-45%);
  background-color: var(--bg);
  border: var(--border);
  border-radius: var(--border-radius);
  padding: 1rem;
  overflow: hidden;
  transition: height var(--speed);
}

.menu-item {
  height: 50px;
  display: flex;
  align-items: center;
  border-radius: var(--border-radius);
  transition: background var(--speed);
  padding: 0.5rem;
}


.menu-item:hover {
  background-color: var(--bg-accent);
}

.icon-right {
  margin-left: auto;
}

.menu-primary-enter {
  position: absolute;
  transform: translateX(-110%);

}
.menu-primary-enter-active {
  transform: translateX(0%);
  transition: all var(--speed) ease;
}

.menu-primary-exit {
  position: absolute;
}
.menu-primary-exit-active {
  transform: translateX(-110%);
  transition: all var(--speed) ease;
}

.menu-secondary-enter {
  transform: translateX(110%);

}
.menu-secondary-enter-active {
  transform: translateX(0%);
  transition: all var(--speed) ease;
}

.menu-secondary-exit {
 
}

.menu-secondary-exit-active {
  transform: translateX(110%);
 
}

/*  END NAVBAR */

/* HOMEPAGE */
.homepageCanvas {
  height: 150px;
  width: 300px;
}
.homepageWrapper {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;

}

/* ABOUT US */

.aboutUsWrapper {
  background-color: #484a4d;
  height: 100vh;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

.about {
  background-color: var(--bg);
  width: 150px;
  height: 150px;
  color: wheat;
  overflow: hidden;
  padding: 1rem;
  border: var(--border);
  box-shadow: var(--boxshadow);
  border-radius: var(--border-radius);
}

.about-primary-enter {
  position: absolute;
  transform: translateY(-110%);
  opacity: 0%;
  
}
.about-primary-enter-active {
  transform: translateY(0%);
  opacity: 100%;
  transition: all 500ms ease;
}

.about-primary-exit {
  opacity: 100%;
}

.about-primary-exit-active {
  transform: translateY(-110%);
  opacity: 0%;
 
}

.about-secondary-enter {
  position: absolute;
  transform: translateY(-110%);
  opacity: 0%;

}
.about-secondary-enter-active {
  opacity: 100%;
  transform: translateY(0%);
  transition: all 500ms ease;
}

.about-secondary-exit {
  opacity: 100%;
}

.about-secondary-exit-active {
  opacity: 0%;
  transform: translateY(-110%);
 
}

@media only screen and (max-width: 600px) {
  .hide-from-sm {
    visibility: hidden;
  }
  body {
    background-color: lightblue;
  }
}
